@import "./_variables";

section.tours {
  background-color: rgb(238, 238, 238);
  padding: 50px;

  .title {
    margin-bottom: 50px;
    h2 {
      font-size: 40px;
      line-height: 60px;
      padding: 15px;
    }
  }
  .work-tours {
    text-align: center;

    a {
      position: relative;
      display: inline-block;
      cursor: pointer;
    }

    img {
      padding: 20px;
    }
     
      .card {
        padding: 30px;
       &.custom-card1 {
          background-color: $egyptcolor;
        }
        &.custom-card2 {
          background-color: $turkeycolor;
        }
        h2.card-title {
          color: white;
        }
        p {
          font-size: 20px;
          color: black;
          padding: 10px;
        }
        .btn-primary,.btn-warning {
          line-height: 30px;
          width: 380px;
          background-color: $secondMainColor;
          border: $secondMainColor;
          transition: all 0.5 ease;
          &:hover {
            background-color: $secondMainColorDark;
            border: $secondMainColorDark;
          }
        }
      }
    }
  }
