footer {
  background-color: #1d1d1d;
  line-height: 60px;
  padding-top: 15px;

  svg {
    font-size: 25px !important;
  }

  ul {
    padding: 0;
    margin: 0;
    li {
      margin-left: 10px;

      a {
        color: grey;
        text-align: center;
        line-height: 40px;
        display: block;
        padding-bottom: 20px !important;
      }
    }
  }
  .copyright p {
    font-size: 20px;
    color: #919191;
    text-align: left;
    line-height: 45px;
    margin: 0px;
  }
}
