@import "./_variables";
section.services {
  padding: 50px 0;
background-color: #eee;
  .title {
    margin-bottom: 50px;
    h2 {
      font-size: 40px;
      line-height: 60px;
    }
  }

  .card {
    background-color: #ffffff;
    width: 80%;
    height: 270px;
    margin-right: 70px;
    margin-bottom: 50px;
    transition: all .5s ease !important;
    border-radius: 20px;
  
    &:hover {
      height: 350px;
    }

    img {
      width: 150px;
      margin: 40px auto 0;
    }
    &:hover .card-block p {
      opacity: 1;
      transition: all .2s ease;
    }
    .card-block {
      h4 {
        font-size: 30px;
        color: #070c0c;
        margin-bottom: 20px;
        line-height: 35px;
      }
      p {
        font-size: 18px;
        line-height: 30px;
        color: $firstMainColor;
        padding: 5px;
        opacity: 0;
      }
    }
  }
}
