    font-family: ar-font;
    src: url(../fonts/Cairo-Regular.ttf);
}

@font-face {
    font-family: logo-font;
    src: url(../fonts/Lalezar-Regular.ttf);
}

body {
    direction: rtl !important;
    font-family: ar-font;
}

a {
    color: black;
}

a:hover {
    text-decoration: none;
    color: black;
}

