**********************/

.navbar-brand img {
    width: 50px !important;
    margin-left: 20px;
}

.custom-form-control, .custom-btn {
    border: 1px solid #eef0ea;
    box-shadow: none;
    border-radius: 2px;
    font-size: 14px;
    padding: 6px 16px 7px 16px;
    color: #868686;
}

.custom-btn:hover {
    background-color: #d4d4d4;
    border: 1px solid #8c8c8c;
    color: #b86d07;
    transition: all 0.3s ease;
}

.catergorie .nav-link {
    border-right: 0;
    border-radius: 0;
}

.navbar-collapse {
    text-align: right;
}

