@media (max-width:1150px) and (min-width:992px) {
    .custom-navbar .navbar-nav a.nav-link {
        margin: 2px;
        padding: 0;
        font-size: 12px;
    }
    /* end navbar */
}

@media (max-width:1199px) {
    .custom-navbar .navbar-nav a.nav-link {
        padding: 8px;
        padding-top: 0px!important;
        margin-left: 8px;
        margin-right: 8px;
        font-size: 14px;
        margin-top: 10px;
    }
    .custom-navbar .navbar-nav a.nav-link.regist {
        margin-top: 2px;
    }
    /* end navbar */
}

@media(max-width:1110) and (min-width:575px) {
    .form-inline .form-control {
        display: inline-block;
        width: 75px!important;
        vertical-align: middle;
    }
    /* end navbar */
}

@media (max-width:991px) {
    .custom-navbar {
        padding: 15px 20px 30px;
    }
    .navbar-header {
        display: block;
        width: 100%;
        margin-bottom: 22px;
        margin-right: 20px !important;
    }
    .custom-navbar .container {
        max-width: 100%;
    }
    .navbar-header .btn-icon {
        float: left;
    }
    .form-inline {
        width: 100%;
        border-top: 1px solid #eee;
        border-bottom: 1px solid #eee;
        padding: 10px 0;
    }
    .form-inline .dropdown {
        display: none;
    }
    .form-inline .custom-form-control {
        width: 90%;
    }
    .form-inline .search-btn {
        width: 10%;
    }
    .custom-navbar a.btn-icon, .custom-navbar button.btn-icon {
        color: #777;
        border: 1px solid #ddd;
        border-radius: 2px;
        padding: 5px 10px 4px;
        background-color: transparent;
        font-size: 18px;
    }
    .custom-navbar .navbar-nav li a.nav-link {
        padding: 10px 15px;
    }
    .navbar-collapse {
        border-top: 1px solid #eee;
        margin-top: 15px;
        padding-top: 8px;
    }
    .navbar-brand {
        float: right;
    }
    .logoText {
        position: absolute;
        top: 20px;
        right: 50%;
    }
    /* end navbar */
    /*header*/
    header {
        margin-top: 100px;
    }
    header .overlay .slider .carousel-inner .carousel-item .carousel-caption h3 {
        font-size: 30px;
    }
    header .overlay .slider .carousel-inner .carousel-item .carousel-caption {
        bottom: 25%;
    }
    /* end header  */
    /*services*/
    /* end services*/
    /* contact*/
    section.contacts .overlay .testimonials .slider .carousel .carousel-item {
        min-height: 390px;
    }
    section.contacts .overlay .contact-info ul.social li a i {
        width: 40px;
        height: 40px;
        line-height: 40px;
    }
    section.contacts .overlay .contact-info .form form .form-group input {
        font-size: 14px;
    }
    /*end contact*/
}

@media(max-width:991px) and (min-width:767px) {
    .title h2 {
        font-size: 30px !important;
        padding: 0px !important;
    }
    section.contacts .overlay .contact-info .form form .form-group textarea {
        width: 78%;
        height: 80%;
    }
    section.contacts .overlay .contact-info .form form .form-group button {
        width: 78%;
    }
    .custom-contact {
        width: 200% !important;
    }
    section.services .card {
        height: 290px;
        margin-right: 40px;
    }
}

@media(max-width:767px) {
    /* contact*/
    section.contacts .overlay .contact-info ul.contact-us li {
        overflow-wrap: break-word;
        display: flex;
        flex-direction: column;
        text-align: center;
        align-items: center;
        /*end contact*/
    }
}

@media (max-width:575px) {
    .title {
        margin-bottom: 40px;
        border-bottom: transparent !important;
    }
    h2 {
        font-size: 25px !important;
    }
    
    .form-inline .custom-form-control {
        width: 80%;
    }
    .form-inline .search-btn {
        width: 20%;
    }
    /* end navbar */
    /* header */
    header .overlay .slider .carousel-inner .carousel-item .carousel-caption {
        left: 2%;
        right: 2%;
    }
    header .overlay .slider .carousel-control-prev, header .overlay .slider .carousel-control-next {
        display: none;
    }
    /* end header */
    .services .card {
        margin-right: 20px !important;
    }
    .services .card img {
        width: 100px !important;
    }
    .services .card h4 {
        font-size: 20px !important;
    }
    .services .card p {
        font-size: 12px !important;
        line-height: 20px;
    }
    .work-tours img {
        padding: 0px !important;
    }
    .work-tours h2 {
        font-size: 20px !important;
    }
    .work-tours p {
        font-size: 12px !important;
    }
    .work-tours a.btn-primary {
        width: 200px !important;
        font-size: 13px !important;
    }
    footer svg, footer p {
        font-size: 15px !important;
    }
    footer p {
        position: absolute;
        left: 20px;
        top: -70px;
    }
    .turkeytours .card {
        height: 385px;
    }
    #pInfoTurkey {
        font-size: 15px !important;
        line-height: 25px;
    }
    .turkeytours .titleTurkey h3 {
        font-size: 22px !important;
    }
    .turkeytours .card h3 {
        font-size: 20px;
    }
    .turkeytours p {
        font-size: 14px;
        line-height: 20px;
    }
}