@import "./_variables";



section.contacts {
    background-image: url("../images/constact\ us.jpg");
    background-position: center center;
    background-size: cover;


 .overlay {
    padding-top: 100px;
    background-color: rgba(0, 0, 0, 0.4);


 .contact-info {
    padding: 50px;
    margin-bottom:0px;


  .title h3 {
    font-size: 30px;
    color: white;
    margin-bottom: 35px;
    line-height: 50px;
}

.form form .form-group {
    position: relative;
    padding: 0;

   input, textarea{
    color: #c2c2c2;
    border: 2px solid #f1f1f1;
    border-radius: 3px;
    font-family: ar-font;
    font-size: 18px;
    line-height: 35px;
    padding-right: 45px;
    margin-bottom: 30px;
    width: 322px;
    height: 50px;

   &:first-of-type{
    margin-right: 20px;
}
   }

  &:before {
    font-family: "Font Awesome 5 Free";
    position: absolute;
    right: 30px;
    top: 13px;
    line-height: 25px;
    font-size: 18px;
    color: $secondMainColor;
    display: inline-block;
}
textarea{
  width: 97%;
  height: 240px;
}
}



  .form form button {
    font-family: ar-font;
    font-size: 15px;
    color: white;
    line-height: 25px;
    background-color: $secondMainColor;
    padding: 10px;
    border: 2px solid $secondMainColor;
    border-radius: 3px;
    width: 97%;
    margin-right: 20px ;
}
ul.contact-us {


 li {
    font-size: 16px;
    line-height: 30px;
    color: white;
}

 svg {
    color: $secondMainColor;
    margin-left: 15px;
    margin-right: 5px;
}
}
  ul {
    padding: 0;
}
 }

 
}
hr{
  width: 350px !important;
}
}
