@import "./_variables";



section.gallery {
    padding: 75px 0 90px;


.title {
    margin-bottom: 35px;


 h2 {
    font-size: 30px;
    color: $firstMainColor;
    line-height: 50px;
    position: relative;


 svg {
    display: block;
    position: absolute;
    right: 48.5%;
    top: -25px;
}
 }}
 .work-gallery {
     a {
        position: relative;
        display: inline-block;
        cursor: pointer;
         .hover {
            background-color: $turkeycolor;
            position: absolute;
            top: 5%;
            left: 5%;
            width: 90%;
            height: 90%;
            color: #fff;
            opacity: 0;
            transition: all .5s ease;
        }
          &:hover .hover {
            opacity: 1;
        }
    }
    
    
    
      .overlay-gallery{
        background-color: rgba(0, 0, 0, .9);
        position: fixed;
        right: 0;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 2000;
        overflow:hidden;
        padding: 5% 10%;
        opacity: -1;
        transition: all 1s ease;
        margin-top: -100vh;
   
   
        &:target {
            opacity: 1;
            margin-top: 0;
        }
        img {
            width: 100%;
            height: 400px;
        }

    }
    
     
    .gallery-text{
            margin-top: 40px;
   h3 {
        font-size: 36px;
        line-height: 52px;
        color: $turkeycolor;
        margin-bottom: 30px;
    }
    
      ul {
        padding-bottom: 20px;
        margin-bottom: 20px;
        border-bottom: 1px solid #efefef;
        padding-right: 0;
    
    
        li a {
        color: #555555;
        text-decoration: none;
    }
}
       p {
        font-size: 24px;
        line-height: 33px;
        margin-bottom: 50px;
        color: #919191;
    }
    
        a.visit {
        font-size: 20px;
        line-height: 50px;
        border-radius: 25px;
        padding: 0px 30px;
        background-color: $turkeycolor;
        color: #ffffff;
        text-decoration: none;
    }
    
      
}
       a.close {
        color: $turkeycolor;
        position: absolute;
        font-size: 50px;
        top: -10%;
        left: -10%;
        font-weight: bold;
        text-decoration: none;
    }
    
      a .hover span {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-size: 3em;
    }
 input {
    display: none;
}

label {
    font-size: 18px;
    line-height: 35px;
    color: #8f9999;
    cursor: pointer;
    margin: 0 7px 40px;
    padding: 10px 30px;
    background-color: #ffffff;
    border: 2px solid #e4eeee;
    border-radius: 2px;
    &:hover {
        background-color: $secondMainColor;
        color: white;
        border: 2px solid $secondMainColor;
    }
}



input.item-type-1:checked~label.label-type-1,
 input.item-type-2:checked~label.label-type-2, 
 input.item-type-3:checked~label.label-type-3,
 input.item-type-all:checked ~ label.label-type-all{
    background-color: $secondMainColor;
    color: white;
    border: 2px solid $secondMainColor;
}
input.item-type-1:checked~.items .item-type-1, 
input.item-type-2:checked~.items .item-type-2, 
input.item-type-3:checked~.items .item-type-3 {
    transform: scale(1.08);
    opacity: 1;
}
input.item-type-1:checked~.items .item:not(.item-type-1), 
input.item-type-2:checked~.items .item:not(.item-type-2), 
input.item-type-3:checked~.items .item:not(.item-type-3) {
    transform: scale(0.8);
    opacity: 0.5;
}
.items .item {
    transition: all 0.6s ease;
}
 }
 img{
     height: 170px;
     width: 255px;
 }
}