@import "./_variables";
@import "./_mixins";
@import "./_navbar";
@import "./_header";
@import "./_services";
@import "./_tours";
@import "./_contact";
@import "./_footer";
@import "./gallery";
@import "./_turkeytours";
@import "./_Info";

hr {
  width: 90% !important;
  background-color: $secondMainColor !important;
  margin-top: 50px;
  margin-bottom: 50px;
}
.title {
  margin-bottom: 40px;
  border-bottom: 1px solid $firstMainColor;
  width: 400px;
}
form .error {
  color: $secondMainColor;
  margin-bottom: 15px;
  padding-right: 30px;
}

.btn-info{
  @include button-style($firstMainColor);
}
.btn-info:hover{
  @include button-style($firstMainColorDark);
}
.btn-danger{
  @include button-style($secondMainColor);

}
.btn-danger:hover{
  @include button-style($secondMainColorDark);
}
.btn-warning{
  @include button-style($turkeycolor);
}
.btn-warning:hover{
  @include button-style($turkeycolorDark);
}
.btn-primary{
  @include button-style($egyptcolor);
}
.btn-primary:hover{
  @include button-style($egyptcolorDark);
}
.text-danger{
  color: $secondMainColor !important;
}
.text-info{
  color: $firstMainColor !important;
}