@import "./_variables";

.custom-navbar {
  border-bottom: 3px solid #eef0ea;
  width: 100%;
  background-color: white;
  padding: 15px 0;

  .logoText{
    font-family: logo-font;
    font-size: 40px;
  }
  .container {
    padding: 0;
  }

  .custom-form-control:focus {
    box-shadow: 0 2px 5px 0 $firstMainColor;
    border-color: $firstMainColor;
  }

  .navbar-header {
    margin-right: 80px;
  }

  .search-btn {
    background-color: $firstMainColor;
    border-color: $firstMainColor;
    color: #fff;
    border-radius: 0;
    padding: 5px 15px 7px 15px;
    font-size: 14px;
    font-weight: 700;
    border-radius: 0;
    &.search-btn:hover,
    &.search-btn:active,
    &.search-btn:focus,
    &.search-btn.active:focus,
    &.search-btn.active:hover {
      background-color: $firstMainColorDark;
      border-color: $firstMainColorDark;
      color: #ffffff;
    }
  }

  .navbar-nav {
    margin-left: 100px;
    margin-top: 4px;

    li a.nav-link {
      color: #555555;
      font-size: 14px;
      padding: 15px;
      &:hover {
        color: black;
      }
    }
     
    li.nav-item .active {
      color: $firstMainColor !important;
      border-bottom: 1px solid $firstMainColor;
      font-weight: 700;
      &:hover {
        background-color: #e7e7e7;
      }
    }

    li:last-of-type {
      margin-top: 8px;
    }
  }
  a.btn-icon {
    padding: 5px 10px 4px;
    color: #777;
    border: 1px solid #ddd;
    background-color: transparent;
    border-radius: 2px;
    margin-left: 10px;
    margin-right: 15px;
    &:hover {
      color: black;
      background-color: #bfbfbf;
    }
  }
}

.nav-item.active {
  border-bottom: 1px solid $firstMainColor !important;
  font-weight: 700;

  a {
    color: $firstMainColor !important;
  }
}